* {
    font-family: 'Quicksand', sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root {
    font-size: 16px;
}

.App {
    width: 100%;
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    margin: auto;
}

.header {
    display: flex;
    flex-flow: column nowrap;
}

.header-logo {
    width: 39px;
    height: auto;
    cursor: pointer;
    transition: all ease 0.5s;
}

.header-logo:hover {
    transform: scale(1.2);
}

.nav-bar {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    padding: .5rem 1rem;
    position: fixed;
    top: 0;
    z-index: 100;
    background-color: rgb(255, 255, 255);
    width: 100%;
    box-shadow: 1px 1px 19px rgb(0, 0, 0);
}

.nav-bar .header-user {
    display: flex;
    flex-flow: row nowrap;
    width: max-content;
    align-items: center;
    font-size: 1.5rem;
    font-weight: lighter;
}

.header-username {
    /* border: .2px dotted rgba(151, 151, 151, 0.363); */
    padding: 4px 11px;
    border-radius: 50px;
}

.header-btn {
    background-color: #0ad2faad;
    height: 90%;
    width: max-content;
    padding: .3rem .5rem;
    border-radius: 50px;
    color: white;
    margin-left: .9rem;
    cursor: pointer;
    opacity: .7;
    border: none;
    transition: all ease 0.4s;
}

.header-btn i {
    font-size: 1.5rem;
}

.header-btn:hover {
    opacity: 1;
}

.nav-bar .header-coins {
    background-color: rgba(220, 220, 220, 0.562);
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    padding: .4rem .5rem;
    border-radius: 250px;
    margin-left: .5rem;
    justify-content: space-between;
    width: max-content;
    cursor: pointer;
    transition: all ease 0.4s;
}

.header-coin-count {
    font-size: 21px;
    padding-left: 7px;
}

.header-coins:hover {
    background-color: rgba(177, 177, 177, 0.5);
    opacity: 1;
}

.coin {
    width: 35%;
    max-width: 45px;
}

.header-img {
    width: 100%;
    box-shadow: 1px 1px 10px rgb(0, 0, 0);
}

.header-img-ctn {
    position: relative;
    /* que no sobrepase el margen */
    margin-top: 56px;
}

.header-title {
    color: white;
    position: absolute;
    top: 60%;
    left: 10%;
    font-size: 4rem;
}


/* Main */

.main {
    width: 100%;
    height: 250px;
    padding: 4% 7%;
    color: #616161;
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    position: relative;
}

.filters-ctn {
    border-bottom: 1px gainsboro solid;
    width: 100%;
    display: flex;
    flex-flow: row wrap;
    padding-bottom: 1.2rem;
    font-size: 1.5rem;
    align-items: center;
}

.filters-ctn .product-index {
    padding-right: 2rem;
    height: max-content;
    padding: .8rem;
}

.sort-title {
    margin-left: 2rem;
    color: #616161b2;
}


/* Ahora filter con hover tambiÃ©n y falta todo products */

.filter {
    background-color: rgba(220, 220, 220, 0.582);
    border-radius: 35px;
    padding: .5rem .8rem;
    font-size: 1.5rem;
    border: none;
    color: #616161b2;
    margin: 0 7px;
    cursor: pointer;
    transition: all ease 0.5s;
}

.selected {
    background-color: #0ad4fa;
    color: white;
}

.filter:hover {
    transform: translate(-2px, -2px);
    /* color: rgb(105, 105, 105); */
}

.filters-ctn .arrows {
    float: right;
    align-self: center;
    margin-left: auto;
    margin-right: 0;
    height: 48px;
    width: 7%;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
}

.filters {
    border-left: 1px solid gainsboro;
}


/* All product product-container and product-card */

.arrows>img {
    cursor: pointer;
    transition: all ease 0.3s;
    opacity: 70%;
}

.arrows>img:hover {
    transform: translate(-1px, -1px);
    opacity: 100%;
}

.product-container {
    width: 100%;
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    margin: auto;
}

.product-container .product-card {
    width: 276px;
    margin: 1rem;
    padding: 1rem;
    border-radius: 5px;
    position: relative;
    box-shadow: 2px 2px 4px 0 rgba(0, 0, 0, 0.10);
    transition: all ease 1s;
}

.product-card:hover>.hover-filter {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: linear-gradient(-180deg, rgba(10, 212, 250, 0.8) 0%, rgba(37, 187, 241, 0.8) 100%);
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    justify-content: center;
    border-radius: 5px;
}

.product-card:hover>.buy-bag {
    z-index: 100;
}

.product-card:hover {
    transform: translate(-2px, -7px);
    box-shadow: 8px 10px 18px 3px rgb(170, 170, 170);
}

.product-img {
    width: 100%;
}

.product-info {
    border-top: gainsboro 1px solid;
}

.category {
    margin-top: .5rem;
    font-size: 1rem;
    font-weight: 400;
    opacity: .5;
}

.product-name {
    font-weight: 400;
}

.buy-bag {
    float: right;
    position: absolute;
    right: 1rem;
    top: 1rem;
    cursor: pointer;
}

.product-more-coins {
    background-color: #616161;
    opacity: .8;
    width: max-content;
    padding: .3rem 0rem .3rem .7rem;
    border-radius: 50px;
    color: white;
    font-size: .7rem;
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
}

.product-more-coins .coin {
    width: 25%;
    margin-left: .3rem;
}

.hover-filter {
    display: none;
}

@keyframes fade-out {
    0% {
        opacity: 1
    }
    25% {
        opacity: 1
    }
    100% {
        opacity: 0
    }
}

.success-ctn {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    justify-content: center;
    border-radius: 5px;
    animation: fade-out linear 4s forwards;
    z-index: 1293;
}

.success {
    background-color: #28e01acc;
}

.fail {
    background-color: rgba(224, 18, 87, 0.719);
}

.fade-out {
    animation: fade-out linear forwards ease-in-out 5s;
}

.success-title {
    color: white;
    font-size: 1.5rem;
    text-align: center;
}

.success-icon {
    font-size: 3rem;
    color: white;
    margin-top: 1rem;
}

.success-ctn:hover>.buy-bag {
    z-index: 1;
}

.cost-ctn {
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    justify-content: center;
    color: white;
    font-size: 2.25rem;
    font-weight: 300;
    width: 100%;
}

.hover-coin {
    width: 15%;
    margin-top: .5rem;
    margin-left: .5rem;
}

.redeem-btn {
    background-color: white;
    border-radius: 50px;
    width: 80%;
    border: none;
    margin: 1rem;
    padding: .6rem;
    font-size: 1.1rem;
    cursor: pointer;
}

.modal-ctn {
    width: 100%;
    height: 100vh;
    background-color: #0799be94;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1001;
}

.add-title {
    width: 100%;
    text-align: center;
    margin-bottom: 1rem;
    font-size: 1.8rem;
    display: flex;
    flex-flow: column nowrap;
    color: rgba(0, 0, 0, 0.397);
}

.add-modal {
    position: fixed;
    width: 50%;
    z-index: 1002;
    background-color: white;
    left: 25%;
    right: 25%;
    top: 20%;
    border-radius: 10px;
    display: flex;
    flex-flow: row wrap;
    padding: 2rem;
    justify-content: space-around;
}


/* .fa-wallet {
    color: rgba(153, 99, 0, 0.705);
    width: 12%;
    padding: 6px 1px;
    border-radius: 50px;
    align-self: center;
} */

.add-btn {
    width: 25%;
    height: 15rem;
    padding: 1rem;
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    justify-content: center;
    box-shadow: 2px 2px 4px 0 rgba(0, 0, 0, 0.10);
    cursor: pointer;
    border-radius: 5px;
    transition: all ease .7s;
}

.close-ctn {
    height: 9%;
    width: 6%;
    position: absolute;
    right: 1rem;
    top: .5rem;
    font-size: 1.5rem;
    cursor: pointer;
    /* border: 1px solid rgba(155, 155, 155, 0.603); */
    border-radius: 50px;
    padding: 4px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: rgba(156, 156, 156, 0.767);
    transition: all ease .4s;
}

.close-ctn:hover {
    background-color: rgba(216, 215, 215, 0.61);
    color: rgba(0, 0, 0, 0.637);
    border: unset;
}

.add-btn .coin {
    width: 80%;
    max-width: none;
    margin-left: 1rem;
}

.add-btn .amount {
    font-size: 2rem;
    font-weight: lighter;
    color: #000000b2;
}

.add-btn:hover {
    transform: translate(-2px, -5px);
    box-shadow: 8px 10px 18px 3px rgb(170, 170, 170);
}

.add-btn:active {
    background-color: rgba(221, 204, 48, 0.555);
    transition: all ease .1s;
}

.error-ctn {
    display: flex;
    flex-flow: column nowrap;
    /* background-color: rgba(18, 224, 172, 0.568); */
    padding: 2rem;
    border-radius: 10px;
    color: rgb(0, 0, 0);
    align-items: center;
    margin-bottom: 3rem;
}

.error-title {
    font-size: 2.8em;
    margin-bottom: .5rem;
}

.error-subtitle {
    font-size: 1.7rem;
    margin-bottom: .5rem;
}

.sk-cube-grid {
    width: 60px;
    height: 60px;
    margin: 90px auto;
}

.sk-cube-grid .sk-cube {
    width: 33%;
    height: 33%;
    background-color: rgb(92, 194, 241);
    float: left;
    -webkit-animation: sk-cubeGridScaleDelay 1.3s infinite ease-in-out;
    animation: sk-cubeGridScaleDelay 1.3s infinite ease-in-out;
}

.sk-cube-grid .sk-cube1 {
    -webkit-animation-delay: 0.2s;
    animation-delay: 0.2s;
}

.sk-cube-grid .sk-cube2 {
    -webkit-animation-delay: 0.3s;
    animation-delay: 0.3s;
}

.sk-cube-grid .sk-cube3 {
    -webkit-animation-delay: 0.4s;
    animation-delay: 0.4s;
}

.sk-cube-grid .sk-cube4 {
    -webkit-animation-delay: 0.1s;
    animation-delay: 0.1s;
}

.sk-cube-grid .sk-cube5 {
    -webkit-animation-delay: 0.2s;
    animation-delay: 0.2s;
}

.sk-cube-grid .sk-cube6 {
    -webkit-animation-delay: 0.3s;
    animation-delay: 0.3s;
}

.sk-cube-grid .sk-cube7 {
    -webkit-animation-delay: 0s;
    animation-delay: 0s;
}

.sk-cube-grid .sk-cube8 {
    -webkit-animation-delay: 0.1s;
    animation-delay: 0.1s;
}

.sk-cube-grid .sk-cube9 {
    -webkit-animation-delay: 0.2s;
    animation-delay: 0.2s;
}

@-webkit-keyframes sk-cubeGridScaleDelay {
    0%,
    70%,
    100% {
        -webkit-transform: scale3D(1, 1, 1);
        transform: scale3D(1, 1, 1);
    }
    35% {
        -webkit-transform: scale3D(0, 0, 1);
        transform: scale3D(0, 0, 1);
    }
}

@keyframes sk-cubeGridScaleDelay {
    0%,
    70%,
    100% {
        -webkit-transform: scale3D(1, 1, 1);
        transform: scale3D(1, 1, 1);
    }
    35% {
        -webkit-transform: scale3D(0, 0, 1);
        transform: scale3D(0, 0, 1);
    }
}

.hidden {
    display: none;
}

.footer {
    margin: 1.5rem 0 2rem 0;
    padding-bottom: 1rem;
}